body {
  font: normal normal 17px/1.235em "Arial", sans-serif; }

.nav-list {
  display: flex;
  margin: 0;
  padding: 0; }
  .nav-list__item {
    list-style: none;
    margin-right: 70px; }
    .nav-list__item:last-child {
      margin-right: 0; }
  .nav-list__link {
    display: block;
    position: relative;
    color: #ffffff;
    text-decoration: none;
    text-transform: uppercase; }
    .nav-list__link::after {
      content: "";
      position: absolute;
      top: 20px;
      left: 0;
      width: 16px;
      height: 2px;
      background: #ffffff;
      transition-duration: 0.2s; }
    .nav-list__link:hover::after {
      width: 100%; }

.header-nav__list {
  padding-right: 43px; }

.main-header {
  display: flex;
  justify-content: space-between;
  margin: 0; }

.main-wrapper {
  padding: 36px 138px 0 138px;
  background: url("../img/main-wrapper-bg.jpg") no-repeat; }
  .main-wrapper--projects {
    background: transparent; }

.body-wrapper {
  display: flex;
  flex-direction: column;
  font-size: 17px;
  background: #0F0F0F;
  height: 5000px; }

.portfolio {
  padding: 138px; }
  .portfolio__list {
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none; }
  .portfolio__item {
    margin-left: 20px; }
    .portfolio__item:first-child {
      margin-left: 0; }
  .portfolio__link {
    color: #fff;
    opacity: 0.7;
    text-decoration: none;
    font-size: 14px; }

.video-row {
  padding: 0 138px; }
  .video-row__list {
    display: flex;
    padding: 0;
    justify-content: space-between; }
  .video-row__item {
    margin: 0;
    position: relative;
    list-style: none; }
  .video-row__inscription {
    position: absolute;
    color: #FFFFFF;
    margin: 0;
    left: -13px;
    bottom: -7px;
    max-width: 315px;
    font-style: bold;
    font-size: 24px;
    line-height: 1.12; }
    .video-row__inscription::before {
      content: "";
      position: absolute;
      background: #FF0202;
      border-radius: 50%;
      width: 21px;
      height: 21px;
      top: 50%;
      left: -27px;
      margin-top: -10px; }

.watch-all {
  display: flex;
  justify-content: center;
  padding: 120px; }
  .watch-all__link {
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    border: solid 1px #fff;
    padding: 4px 25px;
    border-radius: 3px; }

.call-to-action {
  background: url("../img/call-to-action.jpg") no-repeat;
  min-height: 360px; }
  .call-to-action__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px; }

.calculate-price {
  display: flex;
  justify-content: center;
  border-bottom: 0;
  padding-top: 50px;
  color: #fff; }
  .calculate-price__link {
    text-decoration: none;
    color: #000;
    text-transform: uppercase;
    border: solid 1px #fff;
    padding: 4px 25px;
    border-radius: 3px;
    background: #fff; }

.descripter1 {
  max-width: 790px;
  text-transform: uppercase;
  font-weight: bold;
  color: #fff;
  text-align: center;
  font-size: 24px;
  letter-spacing: 1px;
  line-height: 1.3; }

.descripter2 {
  max-width: 455px;
  padding-top: 10px;
  font-size: 18px;
  color: #fff;
  text-align: center; }
